html,
body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}
body {
  font-weight: 300;
  overflow-x: hidden;
}
.Wrap,
.Header,
.Sidebar,
.Content,
.Footer {
  transition: all 0.3s ease-out;
}
.Wrap {
  position: relative;
  width: 100%;
  height: auto;
  min-height: 100%;
}
.Header {
  position: relative;
  min-height: 50px;
}
.Header:before,
.Header:after {
  content: " ";
  display: table;
}
.Header:after {
  clear: both;
}
.Sidebar {
  width: 250px;
}
.Sidebar--left:before {
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: -1;
  width: inherit;
  background: #444;
  border: inherit;
  content: "";
}
.Sidebar--left.is-collapsed {
  left: -250px;
  position: absolute;
}
.Content {
  height: 100%;
}
.Content:before,
.Content:after {
  content: " ";
  display: table;
}
.Content:after {
  clear: both;
}
.Footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fefcfd;
  height: 50px;
}
@media (min-width: 992px) {
  .Wrap {
    float: left;
  }
  .Sidebar--left {
    float: left;
  }
  .Content {
    display: block;
    overflow: hidden;
    width: auto;
  }
  .Sidebar--left:not(.is-collapsed) ~  .Content,
  .Sidebar--left:not(.is-collapsed) ~  .Footer {
    margin-left: 250px;
  }
}
/* BEGIN TOP bar */
.Top .navbar {
  margin-bottom: 0;
  border-top: 3px solid #CD0067;
}
.Top .navbar-brand {
  padding: 0;
}
.Top .Top-nav {
  margin: 10px 0;
  text-align: right;
}
@media (min-width: 768px) {
  .Top .Top-nav {
    float: right;
  }
}
/* END TOP bar */
